import React, { Component } from "react";
import Navbar from "./Navbar";
import TextQuestion from "./TextQuestion";
import Question from "./Question";
import "react-step-progress-bar/styles.css";
import { ProgressBar } from "react-step-progress-bar";
import Swal from "sweetalert2";

class Page4 extends Component {
	constructor(props) {
		super(props);
		this.state = {
			accidents: false
		};
		this.appendAccidentQuestions = this.appendAccidentQuestions.bind(this);
		this.submit = this.submit.bind(this);
	}
	submit(e) {
		e.preventDefault();
		const Toast = Swal.mixin({
			toast: true,
			position: "top-end",
			showConfirmButton: false,
			timer: 2000,
			timerProgressBar: true
		});

		Toast.fire({
			icon: "info",
			title: "Submitting responses..."
		});

		const q14 = document.querySelector("input[name=Q14]:checked").value;
		const q15 = document.querySelector("input[name=Q15]:checked").value;
		const q16 = document.querySelector("input[name=Q16]:checked").value;
		const q17 = document.querySelector("input[name=Q17]:checked").value;
		localStorage.setItem("Q14", q14);
		localStorage.setItem("Q15", q15);
		localStorage.setItem("Q16", q16);
		localStorage.setItem("Q17", q17);
		if (document.getElementsByName("Q18").length !== 0) {
			const q18 = document.getElementById("Q18").value;
			localStorage.setItem("Q18", q18);
		}
		if (document.getElementsByName("Q19").length !== 0) {
			const q19 = document.getElementById("Q19").value;
			localStorage.setItem("Q19", q19);
		}
		if (document.getElementsByName("Q20").length !== 0) {
			const q20 = document.querySelector("input[name=Q20]:checked").value;
			localStorage.setItem("Q20", q20);
		}
		if (this.state.accidents) {
			const q21 = document.getElementById("Q21").value;
			const q22 = document.getElementById("Q22").value;
			const q23 = document.getElementById("Q23").value;
			const q24 = document.getElementById("Q24").value;
			localStorage.setItem("Q21", q21);
			localStorage.setItem("Q22", q22);
			localStorage.setItem("Q23", q23);
			localStorage.setItem("Q24", q24);
		}
		const q25 = document.getElementById("Q25").value;
		const q26 = document.querySelector("input[name=Q26]:checked").value;
		const q27 = document.getElementById("Q27").value;
		localStorage.setItem("Q25", q25);
		localStorage.setItem("Q26", q26);
		localStorage.setItem("Q27", q27);
		console.log(localStorage);
		const requestOptions = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify(localStorage)
		};
		fetch("/submitInsuranceRequest", requestOptions)
			.then(response => response.json())
			.then(data => {
				if (data.success === 1) {
					window.location.href = "/complete";
				}
			});
	}

	componentWillMount() {
		if (!localStorage.getItem("vehicles_data")) {
			window.location.href = "/form/3";
		}
	}

	appendAccidentQuestions() {
		if (this.state.accidents) {
			var accident_questions = (
				<>
					<TextQuestion
						question="No. of accidents that were your fault"
						qid="Q21"
						val={this.existingValue("Q21")}
					/>
					<TextQuestion
						question="No. of accidents that were not your fault"
						qid="Q22"
						val={this.existingValue("Q22")}
					/>
					<TextQuestion
						question="No. of claims"
						sub_question="Weather, vandalism, uninsured motorist damage, etc."
						qid="Q23"
						val={this.existingValue("Q23")}
					/>
					<TextQuestion
						question="No. of tickets/violations"
						qid="Q24"
						val={this.existingValue("Q24")}
					/>
				</>
			);
			return accident_questions;
		}
	}

	checkValue() {
		if (document.querySelector("input[name=Q20]:checked").value === "Yes") {
			this.setState({ accidents: true });
		} else {
			this.setState({ accidents: false });
		}
	}

	existingValue(qid) {
		if (localStorage.getItem(qid)) {
			return localStorage.getItem(qid);
		} else {
			return "";
		}
	}

	back() {
		window.location.href = "/form/3";
	}

	render() {
		var has_insurance = localStorage.getItem("Q1") === "Yes" ? true : false;
		var insurance_questions;
		if (has_insurance) {
			insurance_questions = (
				<>
					<TextQuestion
						question="How long have you been insured?"
						qid="Q18"
						label="No. of years"
						val={this.existingValue("Q18")}
					/>
					<TextQuestion
						question="Current Provider"
						qid="Q19"
						val={this.existingValue("Q19")}
					/>
					<Question
						question="Accident or tickets in the last 3 years"
						optno="2"
						opt1="Yes"
						opt2="No"
						qid="Q20"
						onClick={() => this.checkValue()}
						val={this.existingValue("Q20")}
					/>
				</>
			);
		}
		return (
			<div className="container">
				<Navbar />
				<div className="page">
					<form onSubmit={this.submit}>
						<div className="questions">
							<p className="question" style={{ textAlign: "left" }}>
								Driver Information
							</p>
							<Question
								question="Gender"
								sub_question="Please select the gender listed on your driver's license."
								optno="2"
								opt1="Male"
								opt2="Female"
								qid="Q14"
								val={this.existingValue("Q14")}
							/>
							<Question
								question="Marital Status"
								optno="4"
								opt1="Single"
								opt2="Married"
								opt3="Divorced"
								opt4="Widowed"
								qid="Q15"
								val={this.existingValue("Q15")}
							/>
							<Question
								question="Credit Score"
								optno="5"
								opt1="Excellent (720+)"
								opt2="Good (680-719)"
								opt3="Average (580-679)"
								opt4="Poor (Below 580)"
								opt5="Not sure"
								qid="Q16"
								val={this.existingValue("Q16")}
							/>
							<Question
								question="Highest level of education"
								optno="5"
								opt1="Doctoral"
								opt2="Masters"
								opt3="Bachelors"
								opt4="High School"
								opt5="Other"
								qid="Q17"
								val={this.existingValue("Q17")}
							/>
							{insurance_questions}
							{this.appendAccidentQuestions()}
							<TextQuestion
								question="Email ID"
								sub_question="We promise not to spam you."
								qid="Q25"
								val={this.existingValue("Q25")}
							/>
							<TextQuestion
								question="Phone Number"
								sub_question="We promise not to spam you."
								qid="Q27"
								val={this.existingValue("Q27")}
							/>
							<Question
								question="How did you hear about us?"
								optno="6"
								opt1="Facebook"
								opt2="Google"
								opt3="Twitter"
								opt4="Instagram"
								opt5="Print Media"
								opt6="Other"
								qid="Q26"
								val={this.existingValue("Q26")}
							/>
						</div>
						<div className="next-page" onClick={this.back}>
							&larr; Go Back
						</div>
						<input type="submit" value="Submit" className="submit" />
						<ProgressBar
							percent={80}
							filledBackground="linear-gradient(to right, #56ccf2, #2f80ed)"
						/>
					</form>
				</div>
			</div>
		);
	}
}
export default Page4;
