import React, { Component } from "react";
import Swal from "sweetalert2";

class SubscribeSection extends Component {
	subscribeToUpdates(e) {
		e.preventDefault();
		var email = document.getElementById("user-email").value;
		const requestOptions = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				email: email
			})
		};
		fetch("/subscribeToUpdates", requestOptions)
			.then(response => response.json())
			.then(data => {
				if (data.success === 1) {
					document.getElementById("subscribe-form").reset();
					Swal.fire({
						title: "Subscribed!",
						text: "We'll send you discount offers and updates regularly!",
						icon: "success",
						confirmButtonText: "OK"
					});
				}
			});
	}

	render() {
		return (
			<div className="container">
				<div className="subscribe-section">
					<br />
					<p>
						Subscribe to our email updates to learn more about the auto reform
						in Michigan and get special discounts on your insurance premium.
					</p>
					<form id="subscribe-form" onSubmit={this.subscribeToUpdates}>
						<input
							type="email"
							id="user-email"
							name="user-email"
							className="subscribe-input"
							placeholder="Email Address"
							required
						/>
						<input
							type="submit"
							name="subscribe-submit"
							id="subscribe-submit"
							className="subscribe-submit"
							value="&rarr;"
						/>
					</form>
				</div>
			</div>
		);
	}
}
export default SubscribeSection;
