import React, { Component } from "react";
import Navbar from "./Navbar";
import "react-step-progress-bar/styles.css";
import { ProgressBar } from "react-step-progress-bar";
import TextField from "@material-ui/core/TextField";
import Swal from "sweetalert2";
import {
	EmailShareButton,
	FacebookShareButton,
	LinkedinShareButton,
	TwitterShareButton,
	WhatsappShareButton,
	FacebookMessengerShareButton,
	TwitterIcon,
	FacebookIcon,
	FacebookMessengerIcon,
	LinkedinIcon,
	EmailIcon,
	WhatsappIcon
} from "react-share";

const styles = theme => ({
	cont: {
		display: "flex",
		flexWrap: "wrap"
	},
	textField: {
		marginLeft: theme.spacing.unit,
		marginRight: theme.spacing.unit,
		width: 200
	}
});

class Complete extends Component {
	componentWillMount() {
		if (
			!localStorage.getItem("zip_code") ||
			!localStorage.getItem("insurance_type")
		) {
			window.location.href = "/";
		}
	}

	back() {
		window.location.href = "/";
	}

	scheduleAppt() {
		const requestOptions = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				uid: localStorage.getItem("uid"),
				timestamp: "now"
			})
		};
		fetch("/scheduleAppt", requestOptions)
			.then(response => response.json())
			.then(data => {
				if (data.success === 1) {
					const Toast = Swal.mixin({
						toast: true,
						position: "top-end",
						showConfirmButton: false,
						timer: 3000
					});
					Toast.fire({
						icon: "success",
						title: "Appointment Scheduled!"
					});
					document.getElementById("sched-appt").style.display = "none";
				}
			});
	}

	subForm(e) {
		e.preventDefault();
		var timestamp = document.getElementById("datetime-local").value;
		const requestOptions = {
			method: "POST",
			headers: { "Content-Type": "application/json" },
			body: JSON.stringify({
				uid: localStorage.getItem("uid"),
				timestamp: timestamp
			})
		};
		fetch("/scheduleAppt", requestOptions)
			.then(response => response.json())
			.then(data => {
				if (data.success === 1) {
					const Toast = Swal.mixin({
						toast: true,
						position: "top-end",
						showConfirmButton: false,
						timer: 3000
					});
					Toast.fire({
						icon: "success",
						title: "Appointment Scheduled!"
					});
					document.getElementById("sched-appt").style.display = "none";
				}
			});
	}

	render() {
		return (
			<div className="container">
				<Navbar />
				<div className="page">
					<h1 className="success" style={{ textAlign: "left" }}>
						Done!
					</h1>
					<p className="success-msg">
						Thank you for sharing your information. Congratulations! You qualify
						for a special discount under the new auto insurance reform in
						Michigan.
					</p>
					<div className="post-form-options vertical-flex" id="sched-appt">
						<div>
							<div className="connect" onClick={() => this.scheduleAppt("now")}>
								Connect to an agent now
							</div>
						</div>
						<div className="post-half-child">
							<p>OR</p>
						</div>
						<div className="schedule-appointment post-child vertical-flex">
							<p>Schedule an appointment</p>
							<form className={styles.cont} onSubmit={this.subForm}>
								<TextField
									required
									id="datetime-local"
									label="Please pick a date and time"
									type="datetime-local"
									className={styles.textField}
									InputLabelProps={{
										shrink: true
									}}
								/>
								<br />
								<br />
								<input
									style={{
										margin: "2vh auto",
										float: "none",
										display: "inline-block"
									}}
									type="submit"
									value="Submit"
									className="submit"
								/>
							</form>
						</div>
					</div>
					<br />
					<div className="referral">
						<p>Share on your social media profile to recieve a $5 discount!</p>
						<FacebookShareButton
							url="http://www.micoverageguru.com/"
							quote="Check out Micoverage Guru!"
						>
							<FacebookIcon size={50} round={true} />
						</FacebookShareButton>
						<FacebookMessengerShareButton
							url="http://www.micoverageguru.com/"
							appId="628466041384674"
						>
							<FacebookMessengerIcon size={50} round={true} />
						</FacebookMessengerShareButton>
						<TwitterShareButton
							url="http://www.micoverageguru.com/"
							title="Check out Micoverage Guru!"
						>
							<TwitterIcon size={50} round={true} />
						</TwitterShareButton>
						<LinkedinShareButton
							url="http://www.micoverageguru.com/"
							title="Check out Micoverage Guru!"
						>
							<LinkedinIcon size={50} round={true} />
						</LinkedinShareButton>
						<EmailShareButton
							url="http://www.micoverageguru.com/"
							subject="Check out Micoverage Guru!"
						>
							<EmailIcon size={50} round={true} />
						</EmailShareButton>
						<WhatsappShareButton
							url="http://www.micoverageguru.com/"
							title="Check out Micoverage Guru!"
						>
							<WhatsappIcon size={50} round={true} />
						</WhatsappShareButton>
					</div>
					<div style={{ paddingTop: "5vh" }}></div>
					<div className="next-page" onClick={this.back}>
						&larr; Back to Home
					</div>
					<ProgressBar
						percent={100}
						filledBackground="linear-gradient(to right, #56ccf2, #2f80ed)"
					/>
				</div>
			</div>
		);
	}
}

export default Complete;
