import React, { Component } from "react";
class Question extends Component {
	createElements(n) {
		var elements = [];
		let comp_props = { ...this.props };
		for (var i = 1; i <= n; i++) {
			let var_name = "opt" + i;
			let var_id = comp_props.qid + "-opt" + i;
			console.log(comp_props[var_name]);
			let input_elem;
			if (comp_props.val !== "") {
				if (comp_props.val === comp_props[var_name]) {
					input_elem = (
						<input
							id={var_id}
							name={comp_props.qid}
							type="radio"
							value={comp_props[var_name]}
							onClick={comp_props.onClick}
							defaultChecked
							required
						/>
					);
				} else {
					input_elem = (
						<input
							id={var_id}
							name={comp_props.qid}
							type="radio"
							value={comp_props[var_name]}
							onClick={comp_props.onClick}
							required
						/>
					);
				}
			} else {
				input_elem = (
					<input
						id={var_id}
						name={comp_props.qid}
						type="radio"
						value={comp_props[var_name]}
						onClick={comp_props.onClick}
						required
					/>
				);
			}
			elements.push(
				<div className="md-radio md-radio-inline">
					{input_elem}
					<label for={var_id}>{comp_props[var_name]}</label>
				</div>
			);
		}
		return elements;
	}

	render() {
		return (
			<div className="question-container">
				<div className="question">
					<p>{this.props.question}</p>
					<p className="sub">{this.props.sub_question}</p>
				</div>
				<div className="question-options">
					{this.createElements(this.props.optno)}
					{/* <label for="opt1" className="radio">
						<input
							type="radio"
							name={this.props.qid}
							id="opt1"
							value={this.props.opt1}
							className="hidden"
						/>
						<span className="label"></span>
						{this.props.opt1}
					</label>

					<label for="opt2" className="radio">
						<input
							type="radio"
							name={this.props.qid}
							id="opt2"
							value={this.props.opt2}
							className="hidden"
						/>
						<span className="label"></span>
						{this.props.opt2}
					</label> */}
				</div>
			</div>
		);
	}
}
export default Question;
